body {
  margin: 0;
  font-family: "Arial", sans-serif;
}

.tea {
  margin-top: 51px;
  text-align: center;
}
.user-profile {
  display: flex;
  justify-content: space-around;
  border-radius: 10px;
}

.user-profile img {
  width: 50px;
  padding-right: 20px;
  margin: 0;
  border-radius: 10px;
}

.user-profile h2 {
  font-size: 16px;
  color: #9eacb0;
}

@media (max-width: 775px) and (min-width: 500px) {
  .item-wrap {
    display: none;
  }
}
