.header {
  position: absolute;
  height: 60px;
  width: 100%;
  z-index: 1;
  transition: 0.3s ease-in-out;
  background-color: #0f0c1c6a;
}

.elegant-title {
  font-family: "Lora", serif;
  font-size: 54px;
  font-weight: 700;
  color: #f29302;
  text-align: center;
  margin: 60px 0;
  position: relative;
  padding-bottom: 15px;
}

.elegant-title::after {
  content: "";
  width: 110%;
  height: 1px;
  background: linear-gradient(60deg, transparent, #f29302, transparent);
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  margin-bottom: 12px;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1420px;
  margin: auto;
  height: 100%;
}

.nav-menu {
  display: flex;
  gap: 15px;
  margin-bottom: -10px;
}

.nav-menu a {
  color: #f1f1f1;
  text-decoration: none;
  cursor: pointer;
}

.nav-menu a:active {
  color: #f29302;
}

.hidden {
  transform: translateY(-100%);
  transition: transform 0.2s ease;
}
.nav-item {
  padding: 1rem;
  font-weight: 500;
  list-style-type: none;
  transition: transform 0.3s ease;
}

.nav-item:hover {
  transform: scale(1.1);
}

.nav-item a:hover {
  color: #f29302;
}

.navbar h1 {
  color: #f29302;
  width: 236px;
  text-decoration: none;
  font-size: 22px;
  border-radius: 5px;
  text-align: center;

  margin-left: -18px;
}

.hamburger {
  display: none;
}

.login-btn {
  background-color: transparent;
  color: #f1f1f1;
  font-weight: 600;
  cursor: pointer;
  transition: all 400ms ease;
  font-size: 18px;
  border: none;
  outline: none;
}

.login-btn:hover {
  transform: scale(1.1);
}

.login-btn:hover {
  color: #f29302;
}

.header.scrolled {
  background-color: #1a1a1a;
}

.header.scrolled .nav-item > a,
.header.scrolled .login-btn {
  color: #585656 !important;
}

.header.scrolled .nav-item > a:hover,
.header.scrolled .login-btn:hover {
  color: #f29302 !important;
}

.icon-scrolled {
  color: #f20202;
}

.logo {
  display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  padding: 0;
  margin-top: 10px;
}

.logo-icon {
  margin-top: -20px;
  width: 60px;
  height: auto;
  fill: #ec5f13;
  padding: 0;
}

h1 {
  margin: 0;
  color: rgb(246, 5, 5);
}

.dropdown-content {
  display: none;
  position: absolute;
  background-color: #8d8d8d;
  min-width: 160px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.15);
  z-index: 1;
  border-radius: 5px;
  overflow: hidden;
  margin-top: 8px;
  transition: opacity 0.3s, transform 0.3s;
  opacity: 0;
  transform: translateY(-10px);
}

.dropdown-content a:not(:last-child) {
  border-bottom: 1px solid #777;
}

.dropdown-content a {
  color: #ededed;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  transition: background-color 0.3s;
}

.dropdown-content a:hover {
  background-color: #4a4a4a;
}

.dropdown:hover .dropdown-content {
  display: block;
  opacity: 1;
  transform: translateY(0px);
}

.logout-btn {
  background-color: transparent;
  color: #f1f1f1;
  font-weight: 600;
  cursor: pointer;
  transition: color 0.2s ease;
  font-size: 16px;
  border: none;
  outline: none;
}

.logout-btn:hover {
  color: #f29302;
}

@media (max-width: 1500px) {
  .login-btn {
    margin-right: 50px;
  }

  .logout-btn {
    display: inline-block;
    background-color: transparent;
    color: #f1f1f1;
    margin: 20px;
    padding: 0;
    font-weight: 600;
    cursor: pointer;
    transition: color 0.2s ease;
    font-size: 16px;
    border: none;
    outline: none;
  }
  .navbar {
    display: flex;
    justify-content: space-around;
  }
  .elegant-title::after {
    content: "";
    width: 98%;
    height: 2px;
    background: linear-gradient(60deg, transparent, #f29302, transparent);
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
  }
}

@media (max-width: 1300px) {
  .header {
    position: absolute;
    height: 80px;
    width: 100%;
    top: 0;
  }
  .logout-btn {
    display: inline-block;
    background-color: transparent;
    color: #f1f1f1;
    margin: 10px;
    padding: 0;
    font-weight: 600;
    cursor: pointer;
    transition: color 0.2s ease;
    font-size: 16px;
    border: none;
    outline: none;
  }
  .nav-menu {
    position: fixed;
    left: -100%;
    top: 80px;
    flex-direction: column;
    background-color: rgba(0, 0, 0, 0.9);
    width: 100%;
    transition: 0.3s;
    margin-left: -20px;
  }

  .nav-menu.active {
    left: 20px;
  }

  .nav-item {
    margin: 1.5rem 0;
  }
  .navbar img {
    display: none;
  }
  .navbar .logo-icon {
    display: none;
  }
  .navbar {
    display: flex;
    justify-content: space-around;
  }
  .elegant-title {
    font-family: "Lora", serif;
    font-size: 54px;
    font-weight: 700;
    color: #060606;
    text-align: center;
    margin: 60px 0;
    position: relative;
    padding-bottom: 15px;
  }

  .elegant-title {
    font-size: 20px;
  }
  .hamburger {
    display: block;
  }
  .dropdown-content {
    margin-left: 1220px;
  }
  .active {
    width: 100%;
  }

  .active li {
    text-align: center;
  }
}

@media (max-width: 550px) {
  .navbar h1 {
    width: 190px;
  }
  .navbar img {
    display: block;
  }

  .user-profile h2 {
    display: none;
  }

  .navbar .logout-btn {
    font-size: 10px;
  }
  .dropdown-content {
    margin-left: 95px;
  }
}
