footer {
  background-color: #1a1a1a;
  font-family: Arial, sans-serif;
  padding: 20px 0;
  font-size: 14px;
}

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
  font-family: Arial, sans-serif;
}

.SHrow {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  position: relative;
}

.logo-iconn {
  width: 100px !important;
  height: auto !important;
}

.col-4 {
  flex: 0 0 33.33333%;
  max-width: 33.33333%;
  padding-left: 15px;
  padding-right: 15px;
  box-sizing: border-box;
}

h3 {
  font-family: "Lora", serif;
  font-size: 27px;
  font-weight: 700;
  color: #f29302;
  margin: 0;
  margin-left: -38px;
  margin-top: 10px;
}

h3::after {
  content: "";
  width: 27%;
  height: 2px;
  background: linear-gradient(270deg, transparent, #f29302, transparent);
  position: absolute;
  left: -50px;
  margin-top: 45px;
}

h4 {
  font-size: 18px;
  color: #585656;
  margin-bottom: 15px;
}

p {
  color: #585656;
  line-height: 1.5;
  margin-bottom: 10px;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
}

li {
  margin-bottom: 10px;
}

a {
  text-decoration: none;
  color: #f29302;
  transition: color 0.3s;
}

a:hover {
  color: #f29302;
}

.social-section h4 {
  font-size: 1.5em;
  margin-bottom: 1em;
}

.social-link {
  display: flex;
  align-items: center;
  text-decoration: none;
  color: #585656;
  margin-right: 10px;
  font-size: 1.2em;
}

.social-link:last-child {
  margin-right: 0;
}

.social-icon {
  font-size: 1.5em;
  margin-right: 0.5em;
}

.social-link:hover .social-icon {
  transform: scale(1.1);
}

.useful-links li a {
  text-decoration: none;
  color: #585656;
  transition: color 0.3s, text-decoration 0.3s, transform 0.2s;
}

.useful-links li a:hover {
  color: #f29302;
}

.useful-links li a:active {
  transform: scale(0.95);
}

.col.legal {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #222;
  color: #585656;
  padding: 10px;
  font-size: 14px;
  line-height: 1;
  height: 50px;
  text-align: center;
}

.policy-links {
  display: flex;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}

.policy-links a {
  color: #585656;
  text-decoration: none;
  transition: color 0.3s;
}

.policy-links a:hover {
  color: #f29302;
}

@media (max-width: 768px) {
  .container img {
    width: 600px;
    padding: 20px;
    margin-left: -215px;
    margin-bottom: -78px;
  }

  .container {
    padding: 10px;
  }

  .logo-iconn {
    width: 67px !important;
  }

  .SHrow {
    flex-direction: column;
    align-items: center;
    gap: 15px;
  }

  .col-4 {
    flex: 0 0 100%;
    max-width: 100%;
    padding: 10px;
  }

  h3 {
    font-size: 20px;
    text-align: center;
    margin-left: 0px;
    margin-top: 15px;
  }

  h3::after {
    width: 74%;
    height: 2px;
    background: linear-gradient(270deg, transparent, #f29302, transparent);
    position: absolute;
    left: 53px;
    margin-top: 36px;
  }

  h4,
  p,
  li {
    text-align: center;
  }

  .social-section h4 {
    font-size: 1em;
  }

  .col.legal {
    font-size: 12px;
    padding: 15px 10px;
    height: auto;
  }

  .policy-links {
    flex-direction: column;
    gap: 15px;
    margin-top: 15px;
  }

  .policy-links a {
    font-size: 13px;
    margin-bottom: 10px;
  }

  a {
    font-size: 0.9em;
  }
}
