.Home {
  display: flex;
  justify-content: end;
  align-items: center;
  height: 100vh;
  background: url("../../assets/Background.jpg") no-repeat center center/cover;
  width: 100%;
}

.text {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  width: 100%;
}

.text h1 {
  font-size: 2em;
  color: #333;
}

.text h1 span {
  color: #f29302;
}

.search-container {
  position: relative;
  width: 60%;
  margin-top: 20px;
}

.form-control {
  width: 85%;
  padding: 15px;
  padding-right: 40px;
  font-size: 1em;
  border: none;
  border-radius: 30px;
  box-shadow: 0 0 15px 4px rgba(0, 0, 0, 0.06);
}

.search-icon {
  position: absolute;
  right: 71px;
  top: 25px;
  transform: translateY(-50%);
  color: #333;
  transition: transform 0.3s ease;
}

.search-icon:active {
  transform: scale(0.9);
}

@media (max-width: 50rem) {
  .search-container {
    width: 60%;
    margin-top: 10px;
    margin-left: -73px;
  }
  .Home {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    background: url("../../assets/Background.jpg") no-repeat center center/cover;
  }

  .form-control {
    width: 100%;
    padding-right: 60px;
  }

  .search-icon {
    right: -50px;
    top: 50%;
  }
}
