.demo {
  margin: 5rem auto;
  padding: 1rem;
}

.demo .container {
  margin: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.demo .col-1 {
  padding: 1rem;
  display: flex;
  flex-direction: column;
}

.demo .col-1 p:nth-child(1) {
  font-size: 1.8rem;
  font-style: italic;
}

.demo .col-1 p:nth-child(2) {
  font-size: 3rem;
  font-weight: 600;
  margin: 0.5rem 0;
}

.demo .col-1 p:nth-child(3) {
  font-size: 1.2rem;
  margin-bottom: 1rem;
}

.col-2 iframe {
  margin: auto;
  position: absolute;
}

.button23 {
  display: inline-block;
  padding: 10px 20px;
  background-color: #3498db;
  border: none;
  border-radius: 5px;
  color: #ffffff;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.3s, transform 0.3s;
  margin-right: 290px;
  margin-left: 100px;
}

.button23:hover {
  background-color: #2980b9;
  transform: scale(1.05);
}

.button23:active {
  transform: scale(0.95);
}

@media (max-width: 1300px) {
  .demo {
    margin: 2rem auto;
    padding: 0.5rem;
  }

  .demo .container {
    grid-template-columns: 1fr;
  }

  .demo .col-1 p:nth-child(1) {
    font-size: 1.5rem;
  }

  .demo .col-1 p:nth-child(2) {
    font-size: 2.5rem;
  }

  .demo .col-1 p:nth-child(3) {
    font-size: 1rem;
  }

  .col-2 iframe {
    position: relative;
    width: 100%;
    height: auto;
  }

  .button23 {
    margin: 1rem auto;
    display: block;
    padding: 8px 16px;
    font-size: 14px;
  }
}
