.about {
  background-color: #f8f8f8;
  margin: auto;
  padding: 10px;

  height: 470px;
}

.about .container {
  margin: auto;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.about h2 {
  font-size: 3rem;
  color: #333;
}

.about .col-2 {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 3rem;
}

.about .col-2 p:first-of-type {
  margin: 2rem 0;
}

.about .col-2 p:nth-child(4) {
  font-size: 1.8rem;
  font-style: italic;
  font-weight: 600;
}

.about button {
  margin-top: 1rem;
}

.container img {
  width: 600px;
  padding: 20px;
  margin-left: -140px;
}

.button {
  display: inline-block;
  padding: 10px 20px;
  background-color: #3498db;
  border: none;
  border-radius: 5px;
  color: #ffffff;
  font-size: 16px;
  font-weight: bold;
  text-align: center;
  text-decoration: none;
  transition: background-color 0.3s, transform 0.3s;
}

.button:hover {
  background-color: #2980b9;
  transform: scale(1.05);
}

.button:active {
  transform: scale(0.95);
}

@media (max-width: 1300px) {
  .about {
    padding: 5px;
    height: auto;
  }

  .about .container {
    grid-template-columns: 1fr;
  }

  .container img {
    width: 100%;
    margin-left: 0;
    padding: 10px;
  }

  .about h2 {
    font-size: 2.5rem;
  }

  .about .col-2 {
    padding: 2rem;
    margin-left: -80px;
  }

  .about .col-2 p:first-of-type {
    margin: 1rem 0;
  }

  .about .col-2 p:nth-child(4) {
    font-size: 1.5rem;
  }

  .button {
    padding: 8px 16px;
    font-size: 14px;
  }
}
@media (max-width: 500px) {
  .container img {
    display: none;
  }
  .about .col-2 {
    margin: 0;
  }
}
